const getCrumbs = (portfolio) => {
  const flatPortfolio = portfolio.reduce((prev, cur)=>{
    if (!(prev.crumbs)){
      return [...cur.crumbs]
    }
    return [...prev.crumbs, ...cur.crumbs]
  }, [])
  const uniqueCrumbs = [...new Set(flatPortfolio)]
  return uniqueCrumbs
}

